.wrapModalWindow {
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: fixed;
  bottom: 0;
  max-width: 80%;
  border-radius: 5px;
  background-color: white;
  z-index: 16000;
  box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.555);
}

.modalWindowTexture {
  width: 100%;
  height: 1rem;
  border-radius: 5px 5px 0 0;
  background: url(../../img/modalTexture.png);
}

.modalWindowBody {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3rem 2rem 3rem 2rem;
}

.modalWindowBtn {
  position: absolute;
  right: 10%;
  bottom: 10%;
  border: 0;
  border-radius: 4px;
  padding: 5px;
  border: 1px solid;
  border-radius: 0.25rem;
  cursor: pointer;
  background-color: white;
}
